<template>
  <div class="sz">
    <van-nav-bar
    class="navbar"
    title=""
    left-arrow
    @click-left="onClickLeft"/>
    <div class="leftPadding">
      
    </div>
    <van-cell-group>
      <van-field 
      input-align='right'
      v-model="value"
      label="" readonly />
      <van-field
      label='' readonly :is-link='true'/>
      <van-field
      label='' readonly is-link/>
      <div class="leftPadding">
        
      </div>
      <van-field
      label='' readonly is-link @click="yssz"/>
      <van-field
      label='' readonly is-link @click="yinsi"/>
      <van-field
      label='' readonly is-link/>
      <div class="space"></div>
      <van-field
      label='' readonly is-link  @click="txsz"/>
      <div class="space"></div>
      <van-field
      label='' readonly is-link/>
      <div class="space"></div>
      <van-cell title="" @click="showShare = true" is-link class="share" />
      <van-share-sheet
      v-model="showShare"
      title=""
      :options="options"
      @select="onSelect"
      />
      <div class="space"></div>
    </van-cell-group>
    <van-button 
    type="default" block @click="tuichu"
    ></van-button>
  </div>
</template>
<script>
export default {
  data() {
    return {
      value:JSON.parse(sessionStorage.getItem("info")).phone,
      showShare: false,
      options: [
        { name: '', icon: 'wechat' },
        { name: '', icon: 'weibo' },
        { name: '', icon: 'link' },
        { name: '', icon: 'poster' },
        { name: '', icon: 'qrcode' },
      ],
    }
  },

  methods: {
    onClickLeft(){
      this.$router.go(-1);
    },
    onSelect() {
      this.showShare = false;
    },
    tuichu(){
      sessionStorage.removeItem('info')
      this.$router.push('/dl')
    },
    txsz(){
      this.$router.push('/txsz')
    },
    yssz(){
      this.$router.push('/yssz')
    },
    yinsi(){
      this.$router.push('/yinsi')
    }
  },
}
</script>
<style scoped>
  .share{
    color:#646566
  }
  .leftPadding{
    padding-left: 15px;
    line-height: 30px;
    height: 30px;
    color:#B6B6B6;
    background-color: #f8f8f8;
  }
  .space{
    height: 5px;
    background-color: #f8f8f8;
  }
  .sz{
    
    font-size: 14px;
  }
  button{
    color: #646566;
  }
</style>