<template>
  <div>
    <van-nav-bar
      class="mylogin"
      title=""
      left-text=""
      left-arrow
      @click-left="onClickLeft"
    />
    <van-form @submit="onSubmit" validate-first @failed="onFailed" :show-error="false">
      <van-field
        v-model="username"
        name="username"
        label=""
        placeholder=""
        :rules="[{ pattern, message: '' }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="password"
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' }]"
      />
      <div style="margin: 16px">
        <van-button round block type="info" native-type="submit"
          ></van-button
        >
      </div>
    </van-form>
    <div class="link">
      <router-link class="" to="/register"></router-link>
    </div>
    
  </div>
</template>
<script>
import { dl } from '../../components/axiosAPI'
import { mapState } from 'vuex'
export default {
  data() {
    return {
      username: "",
      password: "",
      pattern:/^[a-zA-Z][a-zA-Z0-9_]*$/,
    };
  },
  methods: {
    onSubmit() {
      dl(this.username,this.password).then((res)=>{
        // console.log(res);
        // console.log(res.data.code);
        if (res.data.result) {
          console.log(res.data.result);
          sessionStorage.setItem('info',JSON.stringify(res.data.result))
          this.$router.push('/')
          let info = res.data.result
          this.$store.commit('edit',info)
        }else{
          this.$toast.fail("");
        }
      })
    },
    onClickLeft() {
      this.$router.push('/');
    },
    onFailed(errorInfo) {
      // console.log('failed', errorInfo);
    },
  },
  computed:{
    ...mapState(['user'])
  }
};
</script>
<style scoped>
  .link{
    margin-top: 30px;
    margin-left: 220px;
  }
</style>