export async function yiyuanaccess(params) {//
    try {
        return await axios.post(`${base}/detail`, qs.stringify({
            'live_name': params
        })); //
    } catch (e) {
        console.log(e, "");
    }
}
export async function zhuce(username, password, sex, phone, idicator, brithday) {//
    try {
        return await axios.post(`/register`, qs.stringify({
            'username': username,
            'password': password,
            'sex': sex,
            'phone': phone,
            "idicator": idicator,
            'brithday': brithday
        })); //
    } catch (e) {
        console.log(e, "");
    }
}
export async function dl(username, password) {//
    try {
        return await axios.get(`/login/${username}&&${password}`); //
    } catch (e) {
        console.log(e, "");
    }
}
export async function erci() {//
    try {
        return await axios.get(`/shouye2`); //
    } catch (e) {
        console.log(e, "");
    }
}