<template>
  <div>
    <van-nav-bar title="" left-arrow @click-left="onClickLeft" />
    <van-cell center title="/" @click="show">
      <template #right-icon>
        <van-switch v-model="checked" size="24" />
      </template>
    </van-cell>
    <p></p>
    <div v-show="isShow">
      <p></p>
      <van-cell center title="">
        <template #right-icon>
          <van-switch v-model="checked2" size="24" />
        </template>
      </van-cell>
      <p></p>
      <van-cell center title="">
        <template #right-icon>
          <van-switch v-model="checked3" size="24" />
        </template>
      </van-cell>
      <p></p>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      checked: false,
      checked2: false,
      checked3: false,
      isShow: false,
      i:1
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    show() {
      this.i++;
      if (this.i % 2 === 0) {
        this.isShow = false;
        this.checked = false
      } else {
        this.isShow = true;
        this.checked = true;
      }
    },
  },
};
</script>
<style scoped>
p {
  margin: 10px 16px;
  color: lightslategrey;
}
</style>